.App { text-align: center; }

.App-header {
  background-color: #001347;
  height: 100px;
  padding: 10px;
  color: white;
  text-align: left;
}

.App-header p { 
  margin: 5px 0px; 
  font-size: 80%; 
}

.App-header div { margin: 30px 0px; }

.App-title {
  font-size: 2.5em;
  margin: 10px 0 0 0px;
}

.create-title, .item-title {
  margin: 0;
  display: inline-block;
  margin-left: 15px;
}

.add {
  font-size: large;
  text-decoration: none;
}

a { text-decoration: none; }

.plus {
  padding: 3px 14px;
  background-color: #cc0066;
  color: #fcb4d8;
  border-radius: 40px;
  -webkit-box-shadow: 1px 1px 5px 1px #7c7c7c;
          box-shadow: 1px 1px 5px 1px #7c7c7c;
  font-size: 240%;
  font-weight: bold;
}

.back {
  padding: 6px 8px;
  font-size: 200%;
  color: #fff;
  background-color: #2f2074;
  border-radius: 30px;
}

.create-form {
  margin-top: 20px;
  text-align: center;
}

.create-form input {
  width: 75%;
  border: none;
  border-bottom: 1px solid #9B9B9B;
  color: #474747;
  font-size: 130%;
  height: 45px;
  padding: 0;
}

.create-form button {
  background-color: #001347;
  border: none;
  color: #fff;
  padding: 11px 18px 11px 18px;
  border-radius: 30px;
  margin-left: 5px;
  font-size: 160%;
}

ul {
  list-style-type: none;
  padding-left: 0;
  margin-top: 0;
}

ul li {
  border-bottom: 1px solid #bdbdbd;
  text-align: left;
  font-size: 110%;
  padding: 20px;
}

ul li a {
  color: #001347;
  margin-top: 10px;
}

@media (min-width: 500px) {
  .create-form input {
    width: 30%;
  }
}



